import React from 'react'
import { useTheme } from '../hooks/useTheme'

// styles
import './Footer.css'

const Footer = () => {
  const { color } = useTheme()

  return (
    <div className='footer' style={{background: color}}>
        <footer>
            <p>Author: <b>Piotr Piasko</b></p>
        </footer>
    </div>
  )
}

export default Footer